import WPAPI from "wpapi";
import { ipcRenderer } from "electron";
import Store from "electron-store";
import { logger } from "./logger";

const store = new Store();

class WordPressService {
  constructor() {
    this.wp = null;
  }

  async initialize() {
    try {
      const blogUrl = store.get("blogUrl");
      if (!blogUrl) {
        throw new Error("Blog URL not configured");
      }

      // Ensure URL has protocol
      const normalizedUrl =
        blogUrl.startsWith("http://") || blogUrl.startsWith("https://")
          ? blogUrl
          : `https://${blogUrl}`;

      logger.info(
        "Initializing WordPress API",
        {
          endpoint: `${normalizedUrl}/wp-json`,
        },
        "WordPressService"
      );

      const credentials = await ipcRenderer.invoke("get-credentials");
      if (!credentials?.username || !credentials?.password) {
        throw new Error("WordPress credentials not found");
      }

      // Create a new WPAPI instance
      this.wp = new WPAPI({
        endpoint: `${normalizedUrl}/wp-json`,
        username: credentials.username,
        password: credentials.password,
      });

      // Test the connection
      await this.wp.users().me();
      logger.info(
        "WordPress API initialized successfully",
        null,
        "WordPressService"
      );
      return true;
    } catch (error) {
      logger.error(
        "WordPress API initialization failed",
        {
          error: error.message,
          stack: error.stack,
          response: error.response?.status,
          endpoint: this.wp?.toString(),
        },
        "WordPressService"
      );
      throw error;
    }
  }

  async createPost(content) {
    if (!this.wp) {
      await this.initialize();
    }

    try {
      logger.info(
        "Attempting to create post",
        {
          endpoint: this.wp.toString(),
          blogUrl: store.get("blogUrl"),
        },
        "WordPressService"
      );

      const post = await this.wp.posts().create({
        title: content.substring(0, 50) + "...",
        content: content,
        status: "publish",
      });

      logger.info(
        "Post created successfully",
        {
          postId: post.id,
          postUrl: post.link,
        },
        "WordPressService"
      );

      return post;
    } catch (error) {
      logger.error(
        "Error creating post",
        {
          status: error.response?.status,
          statusText: error.response?.statusText,
          message: error.message,
          endpoint: this.wp?.toString(),
        },
        "WordPressService"
      );
      throw this.handleApiError(error);
    }
  }

  async testConnection() {
    try {
      await this.initialize();
      const user = await this.wp.users().me();
      logger.info(
        "Connection test successful",
        {
          username: user.name,
          roles: user.roles,
        },
        "WordPressService"
      );
      return user;
    } catch (error) {
      logger.error("Connection test failed", error, "WordPressService");
      throw this.handleApiError(error);
    }
  }

  handleApiError(error) {
    let errorMessage;

    if (error.response?.status === 401) {
      errorMessage =
        "Authentication failed. Please check your credentials in settings.";
    } else if (error.response?.status === 404) {
      errorMessage =
        "WordPress API not found. Please check your blog URL in settings.";
    } else if (
      error.message.includes("Failed to fetch") ||
      error.code === "ENOTFOUND"
    ) {
      errorMessage =
        "Could not connect to the WordPress site. Please check the URL and make sure the site is accessible.";
    } else {
      errorMessage = error.message;
    }

    logger.error(
      "API Error",
      {
        originalError: error,
        handledMessage: errorMessage,
      },
      "WordPressService"
    );

    return new Error(errorMessage);
  }
}

export const wordPressService = new WordPressService();
